.projects {
  color: #ffffff;
  max-width: 1155px;
  width: 100%;
  margin: 0 auto;

  &__title {
    font-family: "DM Sans", sans-serif;
    font-weight: 500;
    font-size: 34px;
    line-height: 40px;
    letter-spacing: 0.25px;
    margin-bottom: 64px;
  }

  &__image {
    object-fit: cover;
    width: 100%;
    height: 100%;
    margin-bottom: 24px;
    &:last-child {
      margin-bottom: 32px;
    }
  }

  .button__icon {
    object-fit: cover;
    margin-right: 5px;
    fill: #ffffff;
    transition: all ease 0.5s;
  }

  &__button {
    text-decoration: none;
    display: flex;
    width: 218px;
    cursor: pointer;
    border-radius: 5px;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    border: 2px solid transparent;
    font-family: "DM Sans", sans-serif;
    color: #ffffff;
    margin: 0 auto;
    transition: all ease-in 0.5s;
    background-color: #5c62ec;
    padding: 16px 20px;
    position: relative;
    margin-bottom: 128px;

    &:hover {
      background-color: transparent;
      border: 2px solid #5c62ec;
    }
  }
}

@media only screen and (max-width: 426px) {
  .projects {
    margin-bottom: 64px;

    &__title {
      font-weight: 400;
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 32px;
    }

    &__image {
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 24px;
      }
    }

    &__button {
      width: 100%;
      justify-content: center;
      margin-bottom: 0;
    }
  }
}
